  }
  main {
    padding: 215px 16px 60px;
    gap: 14px;
    min-height: 100vh;
  }
  main:before {
    height: 365px;
    width: 397px;
    background-size: cover;
    top: initial;
    bottom: -80px;
    right: -220px;
  }
  main h1 {
    font-size: 24px;
  }
  main p {
    font-size: 14px;
  }
